<template>
  <div class="page-choose-house">

    <mt-header title="选择房间">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>


    <div class="box">
      <div class="house-title">
        <i class="common-icon icon-house"></i>
        <span class="house-title-text">选择房间</span>

        <span class="house-title-choose">已选：A202</span>
      </div>

      <div class="house-list">
        <mt-button size="small">A201</mt-button>
        <mt-button size="small">A202</mt-button>
        <mt-button size="small">A203</mt-button>
        <mt-button size="small">A204</mt-button>
        <mt-button size="small">A205</mt-button>
        <mt-button type="primary" size="small">A206</mt-button>
        <mt-button size="small">A207</mt-button>
        <mt-button size="small">A208</mt-button>
        <mt-button size="small">A209</mt-button>
      </div>
    </div>

    <div class="fix-bottom fix-order">
      <div class="order-summary">
        <span class="order-price">合计：¥2320</span>
        <span class="order-extra">共29天  单人间  1个床位</span>
      </div>
      <mt-button @click.native="handleNext" size="small" type="primary">下一步</mt-button>
    </div>

  </div>
</template>

<script>

export default {
  name: 'page-choose-house',

  components: {

  },
  methods: {
    handleNext: function() {
      this.$router.push({ path: 'bed', query: { plan: 'private' }})
    }
  }
};
</script>

<style lang="css">
  .page-choose{
    min-height:450px;
  }

  .house-title{
    height: 44px;
    padding:0 10px;
    font-size: 16px;
    display: block;
    border-bottom:1px solid #E9E9E9;
  }
  
  .house-title-text{
    display: inline-block;
    vertical-align: middle;
    width: 40%;
    height: 40px;
    line-height: 46px;
    color:#1E1E1E;
  }
  .house-title-choose{
    display: inline-block;
    vertical-align: middle;
    width: 40%;
    height: 40px;
    line-height: 46px;
    color:#0977C5;
    float:right;
    text-align:right;
    margin-right:5px;
  }

  .house-list{
    display:flex;
    flex-wrap:wrap;
    align-content: flex-start;
    justify-content: space-between;
    padding:15px 2%;
  }

  .house-list .mint-button{
    width: 20%;
    margin:5px 2%;
    background: #FFFFFF;
    border: 1px solid #D7D7D7;
    border-radius: 4px;
    box-shadow: none;
  }
  .house-list .mint-button--primary{
    color:#fff;
    background: #0977C5;
    border: 1px solid #0977C5;
  }

  .fix-order{
    display:flex;
  }
  .order-summary{
    flex:2;
    padding:4px 15px;
  }

  .order-price{
    display:block;
    clear:both;
    font-size: 16px;
    color: #1E1E1E;
    line-height: 20px;
  }
  .order-extra{
    font-size:12px;
    color: #828282;
  }

  .fix-order .mint-button{
    flex:1;
    background: #0977C5;
  }

</style>




